import * as React from 'react'
import Palette from '../../../pewter/src/Palette'
import RGB from '../../../pewter/src/RGB'

export class Demo extends React.Component {
	render() {
		const src = 'src/assets/images/sample.jpg'
		let image = new Image(220, 220) 
		image.src = src
		let palette: Palette = new Palette()
		console.log('colors:', palette.getColors())
		image.onload = () => {
			palette.setImage(image)
			console.log('colors:', palette.getColors())
		}
		const colors = palette.getColors()

		return (
			<div className='demo-container'>
				<h1>Pewter</h1>
				<h2>Intelligent color palettes from images.</h2>
				<img src={src} />
				<h6>Colors</h6>
				<ul>
					{colors.map((color: RGB) => (
						<li>{color.toCSS()}</li>
					))}
				</ul>
			</div>
		)
	}
}